<template>
  <div v-if="!login" class="back">
    <div class="front">
        <label>Username</label>
        <input style='width:85%; height:18%; margin:2%' type="text" v-model="username">
        
        
        <label>Password</label>
        <input style='width:85%; height:18%; margin:4%' type="text" v-model="password">
        
        <button class="bground btn btn-lg login" @click.prevent="loginFunc">Login</button>
        <button class="bground btn btn-lg login" @click.prevent="registerFunc">Register</button>
    </div>
    
  </div>
</template>

<script>
import axios from 'axios'
export default {
    data() {
        return {
            username : '',
            password : '',
            login : false,
            login_msg : '',
            webURl : 'http://249ac68e9b36.ngrok.io'
        }
    },
    methods: {
        loginFunc(){
            axios.get(this.webURl+'/api/login', { params: { username : this.username, password : this.password } })
                .then(response => {
                    if(response.data == "success"){
                        alert('Login success!')
                        this.login = true
                    }else{
                        this.username = ''
                        this.password = ''
                        alert('Incorrect username or password')
                    }
                })
                .catch(error => {
                    console.log(error)
                })
        },
        registerFunc(){
            axios.get(this.webURl+'/api/register', { params: { username : this.username, password : this.password } })
                .then(response => {
                    if(response.data == "success"){
                        this.username = ''
                        this.password = ''
                        alert('register success, please log in')
                    }else{
                        this.username = ''
                        this.password = ''
                        alert('username had been used')
                    }
                })
                .catch(error => {
                    console.log(error)
                })
        }
    },
    watch: {
        login : function() {
        // Emit this information to the parents component
            this.$emit("login_check", [this.username, this.password, this.login]);
        }
  }
}
</script>

<style scoped>
    .back{
        top: 0;
        position: fixed;
        width: 100%;
        height: 100%;
        background: rgb(0, 0, 0,0.5);
    }
    .front{
        width: 70vw;
        height: 55vw;
        padding: 3.5vw;
        margin: 100px auto;
        border-radius: 5vw;
        background-color: white;
        font-size: 4vw;
    }
    .login{
        height: 10vw;
        font-size: 4vw;
    }
    .bground{
        background-color: #343a40 !important;
        color: white;
    }
</style>


